import React, { useEffect } from "react"
import { Row, Col, Popconfirm, message } from "antd"
import { Link } from "react-router-dom"
import { Heading, Button, Card, Section, Alert, ButtonLink, GifPlayer } from "components"
import { mutateForgotPassword } from "queries/auth"
import { newClient, renderError } from "helpers"
import { useMutation } from "@apollo/react-hooks"
import { Trans } from "@lingui/macro"
import { country } from "App"

function ForgotSent({ location }) {
	const [handleForgot] = useMutation(mutateForgotPassword, {
		client: newClient("account"),
		onCompleted: (data) => {
			if (data) {
				window.location.replace(`/${country}/forgot/sent`)
				message.success(<Trans>The email has been sent once again.</Trans>)
			}
		},
		onError: (err) => renderError(err),
	})

	const emailAddress = (location.state || {}).email || ""

	const handleResendForgot = (e) => {
		e.preventDefault()
		handleForgot({ variables: { email: emailAddress } })
	}

	useEffect(() => {
		if (!(location.state || {}).success) window.location.replace(`/${country}/login`)
	}, [location.state])

	return (
		<Section style={{ marginTop: "4em" }}>
			<Row type="flex" justify="center" align="middle">
				<Col lg={8}>
					<Card title="" autoHeight>
						<Section centered textAlign="center">
							<GifPlayer src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json" width={250} height={250} class="mb2em" />
							<Heading
								content={<Trans>Instructions sent!</Trans>}
								subheader={
									<Trans>
										Great! Now please take a look at your inbox. We've sent you a detail on how to reset your password.
									</Trans>
								}
							/>
						</Section>
						<Row type="flex" justify="center">
							<Col lg={18}>
								<Alert message={<Trans>Don't forget to check your spam/junk folder too :)</Trans>} type="info" showIcon />
							</Col>
						</Row>
						<Section centered>
							<Popconfirm title={<Trans>Send another copy of email?</Trans>} onConfirm={handleResendForgot}>
								<ButtonLink>
									<Trans>I didn't get the email</Trans>
								</ButtonLink>
							</Popconfirm>
						</Section>
						<Section centered paddingHorizontal="normal">
							<Link to={`/${country}`}>
								<Button type="ghost" icon="home">
									&nbsp; <Trans>Back to Home</Trans>
								</Button>
							</Link>
						</Section>
					</Card>
				</Col>
			</Row>
		</Section>
	)
}

export default ForgotSent
